<template>
  <div id="app">

    <!--<div class="uk-container uk-height-large" id="bodying"></div>-->
    <!--<div class="uk-container uk-position-bottom" id="footer"></div>-->

    <app-header></app-header>
    <!--<img src="./assets/logo.png">-->
    <!--<img src="./assets/mobile-phone.png">-->
    <!--<router-view/>-->
    <!--<app-content></app-content>-->
    <app-body></app-body>
