  box-shadow: inset 0px 8px 20px 0px rgba(0,0,0,0.39);
}

#footer {
  background-color: red;
  height: 60px;
  -webkit-box-shadow: 0px -4px 10px -1px rgba(0,0,0,0.29);
  -moz-box-shadow: 0px -4px 10px -1px rgba(0,0,0,0.29);
  box-shadow: 0px -4px 10px -1px rgba(0,0,0,0.29);
}
</style>



